import React from 'react';


  class TwitchVideo extends React.Component {


    constructor(props) {
      super(props);
      this.state = {completeURL: "https://player.twitch.tv/?channel="+props.ChannelName+"&parent="+props.siteSource};
    }
    componentDidMount(){

    }
    render() {
        return (

            <iframe src={this.state.completeURL} frameborder="0" allowfullscreen="true" scrolling="no" width="100%" height="100%"></iframe>
      );
    }
  }

  export default TwitchVideo;
